.skills {
    color: var(--white);
    padding: 5rem 0;
}

.skills h2 {
    font-size: 2rem;
    padding-bottom: 2.5rem;
}

.skills-logos {
    padding: 0 2rem;
}

.skills-logos img {
    height: 50px;
    margin: 1rem;
}


.skills-logos img:first-child {
    height: 60px;
}

@media only screen and (min-width: 1024px) {

    .skills-logos img {
        height: 80px;
        margin: 1rem;
    }
    
    .skills-logos img:first-child {
        height: 90px;
        
    }

}